@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  box-sizing: border-box;
}
.container {
  width: 100%;
  display: flex;
  padding: 20px;
  gap: 20px;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.detail_page_container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 50px;
}
.post_detail_author {
  display: flex;
  justify-content: space-between;
}
.flex_btns {
  display: flex;
  gap: 5px;
  width: 150px;
}
.posts_btns {
  width: 50%;
  text-align: center;
  padding: 10px;
}
.author_class {
  display: flex;
  justify-content: center;
  width: 100%;
}
.img_author {
  width: 50%;
  height: 50%;
  border-radius: 50%;
}
